.item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 272px;
  min-height: 208px;
  gap: 8px;
  cursor: pointer;
}

.price {
  display: flex;
  gap: 8px;
  align-items: center;
}

.name {
  text-align: center;
}

.hide {
  display: none;
}
