.filling {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: move;
}

.filling::before {
  content: url('../../../images/icon-dots.svg');
  width: 24px;
  height: 24px;
}

/* Fix styles of default components: */

.filling > div {
  display: flex;
}

.filling > div > span {
  width: 100%;
}
